import "./css/prism.css";
import "./css/style.css";
import React, { useEffect, useState } from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

//import FccProject from "./views/FccProject";
import RandomMachineQuotes from "./views/RandomMachineQuotes";
import MarkdownPreviewer from "./views/MarkdownPreviewer";
import Calculator from "./views/Calculator";
import Clock from "./views/Clock";
import DrumMachine from "./views/DrumMachine";

function App(props) {
  const [visibility, setVisibility] = useState({
    linksVisibility: "",
    compVisibility: "none",
  });

  useEffect(() => {
    const showLink = () => {
      setVisibility((prev) => ({
        ...prev,
        linksVisibility: "",
        compVisibility: "none",
      }));
    };

    window.history.pushState(null, null, window.location.pathname);
    window.addEventListener("popstate", showLink);
  }, []);

  const showComponent = () => {
    setVisibility((prev) => ({
      ...prev,
      linksVisibility: "none",
      compVisibility: "",
    }));
  };

  return (
    <Router>
      
        
      <div className='fcc-body' style={{ display: visibility.linksVisibility }}>
        <ul>
          <li className="active" > <Link to="random_machine_quote" onClick={showComponent}>
            Random Quote Machine
          </Link></li>
          <li><Link to="markdown_preview" onClick={showComponent}>
            Markdown Preview
          </Link></li>
          <li><Link to="claculator" onClick={showComponent}>
            JS Calculator
          </Link></li>
          <li><Link to="drum_machine" onClick={showComponent}>
            Drum Machine
          </Link></li>
          <li><Link to="clock" onClick={showComponent}>
            25 +5 Clock
          </Link></li>
        </ul>
         
      </div>

        <div style={{ display: visibility.compVisibility }}>
          <Switch>
            <Route path="/clock">
              <Clock />
            </Route>
            <Route path="/drum_machine">
              <DrumMachine />
            </Route>
            <Route path="/claculator">
              <Calculator />
            </Route>
            <Route path="/random_machine_quote">
              <RandomMachineQuotes />
            </Route>
            <Route path="/markdown_preview">
              <MarkdownPreviewer />
            </Route>
          </Switch>
        </div>
      
     
    </Router>
  );
}

export default App;
