import React, { useEffect, useState } from "react";
import { data } from "../data";

const randomQuote = () => {
  return data.radomMachine.quotes[
    Math.floor(Math.random() * data.radomMachine.quotes.length)
  ];
};
const randomColor = () => {
  return data.radomMachine.colors[
    Math.floor(Math.random() * data.radomMachine.colors.length)
  ];
};

//--------------------------------------------------------

function RandomMachineQuotes() {
  const [quote, setQuote] = useState({
    quote:'',
    author:'',
    color:''

  });

  const handleNewQuote = () => {
    setQuote({
      quote:randomQuote().quote,
      author:randomQuote().author,
      color:randomColor()
    });
  };

  useEffect(() => {
    handleNewQuote()
  }, []);

  return (
    <div className="randomQuotes-body" style={{backgroundColor:quote.color,transition: 'all 1s ease-out', }} >
      <div id="quote-box">
        
        <div id="text">
          <p style={{color:quote.color,transition: 'all 1s ease-out', }}>
            <i className="fa fa-quote-left"></i> 
            {'  '}{quote.quote}
          </p>
        </div>

        <div id="author">
          <p>-{quote.author}</p>
        </div>

        <div className="quote-footer">
          <div id="share-quote">
            <a
              

              id="Tumblr-quote"
              className="coustom-btn social-media-button"
              style={{backgroundColor:quote.color}}
              title="Tumblr this quote!"
              target="_top"
              href="https://www.tumblr.com/widgets/share/tool"
            >
              <i className="fa fa-tumblr"></i>
            </a>

            <a
              id="tweet-quote"
              className="coustom-btn social-media-button "
              style={{backgroundColor:quote.color}}
              title="Tweet this quote!"
              target="_top"
              href="https://twitter.com/intent/tweet"
            >
              <i className="fa fa-twitter"></i>
            </a>
          </div>

          <button
            id="new-quote"
            className='coustom-btn'
            style={{backgroundColor:quote.color}}
            onClick={handleNewQuote}
          >
            New
          </button>
        </div>
      </div>
    </div>
  );
}
export default RandomMachineQuotes;
