import React, { useEffect, useState } from "react";

const TimeFormat = (sec) => {
  if (sec === 3600) {
    return "60:00";
  } else return new Date(sec * 1000).toISOString().substr(14, 5);
};

const init = {
  audioBeep: new Audio(),
  session_time: 25,
  break_time: 5,
  cycle: "Session",
  time_left: 25 * 60,

  switch_cycle: function () {
    if (this.cycle === "Session") {
      this.cycle = "Break";
      this.time_left = this.break_time * 60 + 1;
    } else if (this.cycle === "Break") {
      this.cycle = "Session";
      this.time_left = this.session_time * 60 + 1;
    }
  },
};

function Clock() {
  const [timer, setTimer] = useState(init);
  const [intervalId, setIntervalId] = useState(0);

  useEffect(() => {
    if (timer.time_left === 0) {
      timer.switch_cycle();
      timer.audioBeep.play();
    }
   
  }, [timer]);

  const resetSession = () => {
    if (intervalId) {
      clearInterval(intervalId);
      setIntervalId(0);
    }
    timer.audioBeep.pause();
    timer.audioBeep.currentTime = 0;
    setTimer((prev) => ({
      ...prev,
      session_time: 25,
      break_time: 5,
      cycle: "Session",
      time_left: 25 * 60,
    }));
  };

  const toggleStartStop = () => {
    //IF INTERVAL TRUE [NON ZERO] THEN CLEAR AND EXIT
    if (intervalId) {
      clearInterval(intervalId);
      setIntervalId(0);
      //clear setTimeOut()
      return;
    }

    const newIntervalId = setInterval(() => {
      setTimer((prev) => ({
        ...prev,
        time_left: prev.time_left - 1,
      }));
    }, 1000);

    //SAVE THE INTERVAL ID TO CLEAR IT LATER
    setIntervalId(newIntervalId);
  };

  ////////////////////////////////////////////////////
  /// SET BREAK AND SESSION LENGTH
  const breakIncrement = () => {
    if (timer.break_time !== 60) {
      setTimer((prev) => ({
        ...prev,
        break_time: prev.break_time + 1,
      }));
    }
  };
  const breakDecrement = () => {
    if (timer.break_time !== 1) {
      setTimer((prev) => ({
        ...prev,
        break_time: prev.break_time - 1,
      }));
    }
  };

  const sessionIncrement = () => {
    if (timer.session_time !== 60) {
      setTimer((prev) => ({
        ...prev,
        session_time: prev.session_time + 1,
        time_left: (prev.session_time + 1) * 60,
      }));
    }
  };

  const sessionDecrement = () => {
    if (timer.session_time !== 1) {
      setTimer((prev) => ({
        ...prev,
        session_time: prev.session_time - 1,
        time_left: (prev.session_time - 1) * 60,
      }));
    }
  };

  /////////////////////////////////////////////////////////
  ////////////
  return (
    <div className="clock-body">
      <div className="clock-container" >
        
      <div className="clock-container__break">
        <h1 id="break-label">Break Length</h1>
        <button  id="break-decrement" onClick={breakDecrement}>
        <i className="fas fa-angle-double-left"></i>
        </button>
        <h3  id="break-length"> {timer.break_time} </h3>
        <button id="break-increment"  onClick={breakIncrement}>
        <i className="fas fa-angle-double-right"></i>
        </button>
      </div>

      <div className="clock-container__session">
        <h1 id="session-label">Session Length</h1>
        <button  id="session-decrement" onClick={sessionDecrement}>
        <i className="fas fa-angle-double-left"></i>
        </button>
        <h3 id="session-length"> {timer.session_time} </h3>
        <button  id="session-increment" onClick={sessionIncrement}>
        <i className="fas fa-angle-double-right"></i>
        </button>
      </div>

      <div className={`clock-container__${timer.time_left>61?'display':'warning'} `}>
        <h1 id="timer-label"> {timer.cycle} </h1>
        <h1 id="time-left" className='clock-container__display-run'> {TimeFormat(timer.time_left)} </h1>
      </div>

      <div className="clock-container__controls">
        <button id="start_stop" onClick={toggleStartStop}>
        <i className="far fa-play-circle"></i>
        {' '}
        <i className="far fa-stop-circle"></i>
        </button>
        <button id="reset" onClick={resetSession}>
        <i className="fas fa-sync-alt"></i>
        </button>
      </div>

      <div className="clock-container__author">
        {" "}
        Designed and Coded by <br/>
        <a href="https://github.com/mokhtarTarek">Mokhtar Tarek</a>
      </div>
      <audio
        id="beep"
        preload="auto"
        ref={(audio) => {
          timer.audioBeep = audio;
        }}
        src="https://raw.githubusercontent.com/freeCodeCamp/cdn/master/build/testable-projects-fcc/audio/BeepSound.wav"
      />
      </div>
      
    </div>
  );
}

export default Clock;
