import React, { useEffect, useState } from "react";
import { data } from "../data";

//////////////////////////////////////
//////DATA
const bankOne = data.drumMachine.bankOne;
const bankTwo = data.drumMachine.bankTwo;

//////////////////////////////////////
////// DRUM COMPONENT
const Drumpad = (props) => {
  useEffect(() => {
    const handleKeyDown = (event) => {
      let audioKey = String.fromCharCode(event.keyCode);
      if (audioKey === props.keyTrigger) {
        props.playAudio(audioKey, props.id);
      }
    };

    window.addEventListener("keydown", handleKeyDown);
    // cleanup
    return () => {
      window.removeEventListener("keydown", handleKeyDown);
    };
  }, [props]);

  return (
    
      <button
        className="drum-pad drum-machine__pad"
        id={props.id}
        key={props.keyCode}
        onClick={() => props.playAudio(props.keyTrigger, props.id)}
      >
        <audio id={props.keyTrigger} src={props.url} className="clip" />

        {props.keyTrigger}
      </button>
   
    
  );
};

function DrumMachine() {
  const [power, setPower] = useState(true);
  const [sound, setSound] = useState({
    name: "Heater",
    value: 0.3,
  });

  const [kit, setKit] = useState({
    name: "Heater",
    bank: bankOne,
    val: true,
  });

  const playAudio = (key, audioName) => {
    if (power) {
      const audio = document.getElementById(key);

      //console.log(audioName)
      audio.volume = sound.value;
      audio.currentTime = 0;
      audio.play();

      setSound((prev) => ({
        ...prev,
        name: audioName.replace(/-/g, " "),
      }));

      console.log(audioName);
    }
  };

  const handlePower = () => {
    console.log(power);
    setPower(!power);
  };

  const handleBank = () => {
    setKit((prev) => {
      console.log("jjj");
      if (prev.val) {
        return {
          ...prev,
          name: "Piano",
          bank: bankTwo,
          val: !prev.val,
        };
      } else {
        return {
          ...prev,
          name: "Heater",
          bank: bankOne,
          val: !prev.val,
        };
      }
    });
  };

  const handleVolume = (e) => {
    setSound((prev) => ({
      ...prev,
      value: e.target.value,
    }));
  };

  return (
    <div className="drum-machine-body">
      <div className="drum-machine" >
        <div id="drum-machine" className="drum-machine__drumPad">
          {kit.bank.map((drum) => (
            <Drumpad
              key={drum.id}
              id={drum.id}
              keyCode={drum.keyCode}
              keyTrigger={drum.keyTrigger}
              url={drum.url}
              playAudio={playAudio}
            />
          ))}
        </div>
        <div className="drum-machine__display" id="display">
          <div>
            <h1>{sound.name}</h1>
          </div>
          <div className="control-volume">
            <h2> {"Volume: " + Math.round(sound.value * 100)} </h2>
            <input
              type="range"
              min="0"
              max="1"
              step="0.01"
              onChange={handleVolume}
              value={sound.value}
            />
          </div>

          <div className="control-power">
            <h2> {power ? "Power On  " : "Power Off  "} </h2>

            <div className="control-power__container" onClick={handlePower}>
              <div
                className={`control-power__slider  ${
                  power
                    ? "control-power__slider-active"
                    : "control-power__slider-inactive"
                }  `}
              />
            </div>
          </div>

          <div className="control-power">
            <h2> {`Kit ${kit.name} `} </h2>

            <div className="control-power__container" onClick={handleBank}>
              <div
                className={`control-power__slider  ${
                  kit.val
                    ? "control-power__slider-active"
                    : "control-power__slider-inactive"
                }  `}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default DrumMachine;
